

.wrapper {
  width: 100%;
  height: 100vh;
  padding-top: 40px;
  font-family: "Gilroy", sans-serif;
}

.container {
  max-width: 990px;
  margin: 0 auto;
  padding: 0 15px;
}

.logo {
  width: 220px;
  height: 40px;
  margin-bottom: 44px;
}

.title {

  font-weight: 500;
  font-size: 36px;
  line-height: 48px;
  color: #212121;
  margin-bottom: 28px;
}

.description {
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  color: #212121;
  margin-bottom: 45px;
}

.form {
  margin-bottom: 55px;
}

.formSubtitle {
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  margin-bottom: 15px;
  color: #F02619;
}

.formInner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}
.text {
  color: green;
}

.text3 {
  color: #F02619;
}

.formInput {
  max-width: 710px;
  width: 100%;
  height: 80px;
  border: 1px solid #212121;
  padding: 25px 40px;
}

.formBtn {
  width: 240px;
  height: 80px;
  border: none;
  background: #d92619;
  font-weight: bold;
  font-size: 20px;
  line-height: 30px;
  cursor: pointer;
  color: #FFFFFF;
}

.formBtn:hover {
  background: rgba(200, 38, 25);
}


.startInfo {
  text-align: center;
  margin-bottom: 20px;
}

.startInfoTitle {
  font-weight: 500;
  font-size: 36px;
  line-height: 48px;
  color: #212121;
  margin-bottom: 26px;
}

.timer {
  display: flex;
  justify-content: center;
}

.timerItem {
  width: 80px;
}

.num {
  font-weight: 500;
  font-size: 36px;
  line-height: 48px;
  color: #212121;
}

.days {
  font-weight: 500;
  font-size: 14px;
  line-height: 30px;
  color: #A0A0A0;
}

.footer {
  display: flex;
  padding-top: 19px;
  border-top: 1px solid #B6B6B6;

}

.footerInfo,
.footerEmail{
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  color: #212121;
  margin-right: 40px;
}

.footerEmail {
  margin-right: 0;
}

@media (max-width: 320px) {
  .logo {
    width: 130px;
    height: 24px;
    margin-bottom: 35px;
  }
  .title {
    font-size: 24px;
    line-height: 32px;
    margin-bottom: 17px;
  }
  .description {
    font-size: 18px;
    line-height: 28px;
    margin-bottom: 44px;
  }
  .form {
    margin-bottom: 24px;
  }
  .formSubtitle {
    font-size: 18px;
    line-height: 28px;
    margin-bottom: 0;
    text-align: center;
  }
  .formInner {
    flex-direction: column;
  }
  .formInput {
    height: 60px;
    margin-bottom: 10px;
  }
  .formBtn {
    height: 60px;
    width: 100%;
  }
  .startInfo {
    margin-bottom: 49px;
  }

  .startInfoTitle {
    font-size: 24px;
    line-height: 32px;
    margin-bottom: 9px;
    text-align: center;
  }
  .timerItem {
    width: 70px;
  }
  .num {
    font-size: 30px;
    line-height: 48px;
  }
  .days {
    font-size: 12px;
    line-height: 30px;
  }
  .footer {
    flex-direction: column;
  }
  .footerInfo,
  .footerEmail {
    font-size: 14px;
    line-height: 24px;
  }
}